export default {
	data() {
		return {
			agree: false,
		}
	},
	onLoad(res){
		if(this.token !== ""){
			uni.reLaunch({
				url: '/pages/main/main'
			})
		}
	},
	methods: {
		login: function() {
			if (!this.agree) {
				this.toast('请先同意《用户协议》和《隐私协议》');
				return;
			}
			uni.navigateTo({
			    url: './login'
			});
		},
		qqLogin: function() {
			uni.showToast({
				icon: 'none',
				title: '正在开发中,敬请期待...',
				position: 'bottom',
				duration: 1500,
				mask: true
			});
		},
		weixinLogin: function() {
			uni.showToast({
				icon: 'none',
				title: '正在开发中,敬请期待...',
				position: 'bottom',
				duration: 1500,
				mask: true
			});
		},
		weiboLogin: function() {
			uni.showToast({
				icon: 'none',
				title: '正在开发中,敬请期待...',
				position: 'bottom',
				duration: 1500,
				mask: true
			});
		}
	}
}
